.App {
  text-align: center;
  background-color: #455a64;
  min-height: 100vh;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.inline-block {
  display: inline-block;
}

.flex-button {
  position: fixed;
  top: 30%;
  left: 0;
  font-weight: 700;
  font-size: 18px;
}

.flex-area {
  display: flex;
  align-items: center;
  margin: 50px;
}

h4 {
  margin: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}